
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapGetters } from 'vuex'
export default {
name: 'app',
components: {
Loading
},
computed: {
...mapState({
isLoading: state => state.vux.isLoading
}),
...mapGetters('app', {
direction: 'direction'
}),
viewTransition () {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
}
}



